.cards {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  list-style: none;
  padding: 0;
  margin: 0;
}

@media screen and (max-width: 619px) {
  .cards {
    flex-direction: column;
  }
}

@media screen and (max-width: 424px) {
  .cards {
    justify-content: center;
  }
}